@import "../../utils/variables.scss";

.wrapper {
  display: flex;
  background-color: #f5f5f5;
  min-height: 100vh;
  justify-content: center;
  box-shadow: inset 1px 0px 4px 2px rgba(0, 0, 0, 0.25);
  padding-top: 70px;


  .inner {
    display: flex;
    background-color: white;
    width: 100%;
  }


  .left {
    width: 40%;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  .right {
    padding: 50px;
    width: 55%;


    .registerButton {
      display: flex;
      justify-content: end;
      color: #7a86a1;

      span {
        color: #4b9bee;
        cursor: pointer;
      }
    }

    .text {
      font-size: 30px;
      font-weight: 500;
      margin-top: 30px;
      margin-bottom: 30px;
    }

    .textGroup {
      display: flex;
      gap: 20px;
      margin-top: 20px;
      position: relative;
      flex-wrap: wrap;

      div {
        width: 100%;
        position: relative;
        max-width: 300px;


        input {
          width: 100%;
          height: 50px;
          border-radius: 20px;
          border: 1px solid #ebebeb;
          padding-left: 15px;

        }

        label {
          font-size: 0.9rem;
          margin-bottom: 0.5em;
          display: inline-block;
          font-weight: 500;
        }

        svg {
          position: absolute;
          right: 35px;
          color: #c0bdcc;
          width: 17px;
          top: 2.5em;
          cursor: pointer;
          z-index: 2;

        }

        .checkboxContainer {
          display: flex;
          align-items: center;
          gap: 10px;
          margin-top: 10px;

          input {
            width: 20px;
            height: 20px;
          }

          label {
            font-size: 0.8rem;
            margin-bottom: 0;
          }
        }

        .link {
          color: #4b9bee;
          cursor: pointer;
          font-size: .8rem;
          display: inline-block;
          width: unset;
          margin-left: 20px;
        }

        button {
          border-radius: 20px !important;
          box-shadow: 3.99px 22.65px 57px 0 #36363642;
        }

      }

    }

    .usernameError {
      text-align: left;
      font-size: 0.8rem;
      color: red;
    }

  }

}

@media screen and (max-width: 900px) {
  .wrapper {
    .left {
      display: none;
    }

    .right {
      width: 100%;
    }
  }
}