@import "../../utils/variables.scss";

.wrapper {
  display: flex;
  width: 100%;
  min-height: 100vh;
  background-color: #f5f5f5;
  box-shadow: inset 1px 0px 4px 2px rgba(0, 0, 0, 0.25);

  .textField {
    width: 100%;
  }

  .checkbox {
    width: 50px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    bottom: -15px;
    left: 50%;
    transform: translateX(-50%);
  }

  input {
    width: 100%;
    height: 50px;
    border-radius: 20px;
    border: 1px solid rgb(235, 235, 235);
    padding-left: 15px;
  }

  input[type="tel"] {
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1) !important;
    border: none;
    margin-bottom: 1em;
  }

  label {
    font-size: 0.9rem;
    margin-bottom: 0.5em;
    display: inline-block;
    font-weight: 500;
  }

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }


  .selectField {
    margin-bottom: 2em;
  }

  .mainCard {
    display: flex;
    width: 100%;
    background-color: white;
    box-shadow: 0 1.5px 39px 0 rgba(0, 0, 0, 0.16);
    border-radius: 20px;
    padding-top: 70px;

    .left {
      width: 40%;
      display: flex;
      justify-content: center;
      align-items: center;

    }

    .right {
      width: 55%;
      padding: 50px;

      .alreadyRegistered {
        display: flex;
        justify-content: flex-end;
        align-items: flex-end;
        padding: 10px;
        color: #7a86a1;

        span {
          cursor: pointer;
          margin-left: 5px;
          color: #4b9bee;
        }
      }

      .welcome {
        .title {
          margin-top: 20px;
          margin-bottom: 20px;
          font-size: 30px;
          font-weight: 500;
        }
      }

      .logo {
        display: flex;
        flex-direction: column;
        align-items: center;

        .image {
          margin-bottom: 10px;

          img {
            width: 130px;
          }
        }
      }

      .error {
        width: 100%;
        color: red;
        font-size: 12px;
        margin-top: 3px;
        text-transform: capitalize;
      }

      .deptError {
        margin-bottom: 30px;
      }

      .usernameError {
        margin-top: -20px !important;
        margin-bottom: 10px;
        text-align: left;
      }

      .selectors {
        margin: 10px 0 35px 0;
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        gap: 20px;

        .selectorPair {
          @media screen and (max-width: 1200px) {
            flex-direction: column;
          }

          display: flex;
          margin-bottom: 1px;

          &:last-child {
            .selector:last-child {
              margin-right: 0;
            }
          }
        }

        .selector {
          box-shadow: 0 1px 2px 0px rgba(0, 0, 0, 0.09);
          border-radius: 15px;
          padding: 12px;
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: start;
          border: 1px solid #ebedf0;

          .title {
            padding-left: 33px;
            margin-top: 5px;
            font-size: 13px;
            color: #575757;
          }

          .name {
            text-align: center;
            font-size: 15px;

            span {
              padding-top: 0;
              padding-bottom: 0;
            }
          }


        }

        .selector1 {
          box-shadow: 0 1px 2px 0 rgba(0, 215, 151, 1);
          border-radius: 15px;
          padding: 12px;
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: start;
          border: 1px solid #ebedf0;

          .title {
            padding-left: 33px;
            margin-top: 5px;
            font-size: 13px;
            color: #575757;
          }

          .name {

            text-align: center;
            font-size: 15px;

            span {
              padding-top: 0;
              padding-bottom: 0;
            }
          }


        }

        .selector2 {
          box-shadow: 0 1px 2px 0 rgba(145, 61, 237, 1);
          border-radius: 15px;
          padding: 12px;
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: start;
          border: 1px solid #ebedf0;

          .title {
            padding-left: 33px;
            margin-top: 5px;
            font-size: 13px;
            color: #575757;
          }

          .name {
            text-align: center;
            font-size: 15px;

            span {
              padding-top: 0;
              padding-bottom: 0;
            }
          }


        }

        .selector3 {
          box-shadow: 0 1px 2px 0 rgba(237, 213, 61, 1);
          border-radius: 15px;
          padding: 12px;
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: start;
          border: 1px solid #ebedf0;

          .title {
            padding-left: 33px;
            margin-top: 5px;
            font-size: 13px;
            color: #575757;
          }

          .name {

            text-align: center;
            font-size: 15px;

            span {
              padding-top: 0;
              padding-bottom: 0;
            }
          }


        }
      }

      form {

        input[type="radio"] {
          width: 12px;
          height: 12px;
        }

        .errors {
          color: red;
          font-size: 12px;
        }

        .textGroup {
          display: flex;
          gap: 20px;
          margin-top: 20px;
        }

        .agreeText {
          color: #575757;
          font-size: 14px;
          margin-right: 20px;
          margin-bottom: 30px;

          span {
            color: #4b9bee;
            cursor: pointer;
          }
        }

        .row {
          position: relative;
          width: 100%;
          margin-bottom: 20px;


          svg {
            position: absolute;
            right: 15px;
            bottom: 48px;
            width: 17px;
            cursor: pointer;
          }



          .firstName {
            margin-right: 10px;
          }

          .textField {
            flex: 1;
            margin-right: 30px;

            &:last-child {
              margin-right: 0;
            }


          }
        }

        .row1 {
          position: relative;
          width: 100%;
          margin-bottom: 20px;

          svg {
            position: absolute;
            right: 15px;
            color: #c0bdcc;
            width: 17px;
            top: 2.5em;
            cursor: pointer;
          }



          .firstName {
            margin-right: 10px;
          }

          .textField {
            flex: 1;
            margin-right: 30px;

            &:last-child {
              margin-right: 0;
            }


          }
        }

        .checkboxContainer {
          margin: 20px 0;
        }

        .btnContainer {
          display: flex;
          justify-content: space-between;
          align-items: flex-end;

          .Button {
            box-shadow: 4px 22.6px 57px 0px rgba(54, 54, 54, 0.1);
          }

          div {
            color: #575757;
            font-size: 14px;
            margin-right: 20px;

            span {
              border-bottom: 1px solid #575757;
              color: $themePurple;
              font-weight: 500;
              cursor: pointer;
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 900px) {
  .wrapper {
    .mainCard {
      flex-direction: column;

      .left {
        display: none;
      }

      .right {
        width: 100%;
        padding-top: 50px;
        padding-bottom: 50px;
        padding-left: 3em;
        padding-right: 3em;
        width: 100% !important;
        max-width: unset;

        .selectors {
          width: 100%;

        }
      }
    }
  }
}

@media screen and (max-width: 600px) {
  .wrapper {
    padding: 0;

    .mainCard {
      padding-bottom: 3em;


      .right {
        padding-inline: 1em;

        .selectors {
          flex-direction: column;
          gap: 20px;

          .selectorPair {
            .selector {
              min-width: 105px;

              .name {
                padding-top: 0 !important;
              }

              .title {
                padding-top: 10px;
                justify-content: center;
              }
            }

            .selector:last-child {
              margin-right: 0;
            }
          }
        }

        form {
          .row {
            flex-direction: column;
          }
        }
      }
    }
  }
}